import { collection, onSnapshot, orderBy, query, where } from "firebase/firestore";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { db } from "../firebase/config";

import Tweet from "./Tweet"

export default function ProfileLikes() {
    const { userId } = useParams();
    const [tl, setTl] = useState([]);


    useEffect(()=>{
      const q = query(collection(db,'timeline'),where("likes", "array-contains", userId),orderBy('timestamp','desc'))
      const unsub = onSnapshot(q, (querySnapshot) => {
        setTl(querySnapshot.docs)
      });
      return unsub
    },[])

    return (
      <div className="w-full">
        {tl.map(tweet=>{
          const data = {id:tweet.id,...tweet.data()};
          const {timestamp, ...dataWithoutTS} = data
          return <Tweet key={tweet.id} data={{...dataWithoutTS, timestamp:timestamp&&timestamp.toDate().toLocaleDateString()}} />
        })}
        <div className="h-52"></div>
      </div>
    )
}
